*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-main);
}

img {
  display: inline-block;
}

h1,
h2,
h3 {
  margin-top: 0;
  letter-spacing: 1px;
}

h1 {
  font-size: 1.375rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.625rem;
  }
}

h2 {
  font-size: 1.25rem;
  margin-bottom: 1.375rem;
}
@media (min-width: 40em) {
  h2 {
    font-size: 1.375rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.big-text {
  font-size: 2rem;
  font-weight: bold;
  color: var(--text-main);
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(225deg, hsl(329, 70%, 58%), hsl(37, 97%, 70%));
  --youtube: hsl(348, 97%, 39%);
  --dark-toggle: linear-gradient(225deg, hsl(146, 68%, 55%), hsl(210, 78%, 56%));
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-blue-text: hsl(228, 34%, 66%);
  --dark-white-text: hsl(0, 0%, 100%);
  --light-toggle: hsl(230, 22%, 74%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-gray-text: hsl(228, 12%, 44%);
  --light-blue-text: hsl(230, 17%, 14%);
  --background: var(--dark-bg);
  --text-main: var(--dark-white-text);
  --text-sec: var(--dark-blue-text);
  --toggle: var(--dark-toggle);
  --card: var(--dark-card);
  --card-hover: var(--dark-card-hover);
}

@media (prefers-color-scheme: light) {
  :root {
    --background: var(--light-bg);
    --text-main: var(--light-blue-text);
    --text-sec: var(--light-gray-text);
    --toggle: var(--light-toggle);
    --card: var(--light-card);
    --card-hover: var(--light-card-hover);
  }
}
.body.dark {
  --background: var(--dark-bg);
  --text-main: var(--dark-white-text);
  --text-sec: var(--dark-blue-text);
  --toggle: var(--dark-toggle);
  --card: var(--dark-card);
  --card-hover: var(--dark-card-hover);
}

.body.light {
  --background: var(--light-bg);
  --text-main: var(--light-blue-text);
  --text-sec: var(--light-gray-text);
  --toggle: var(--light-toggle);
  --card: var(--light-card);
  --card-hover: var(--light-card-hover);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 68.75rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.big-cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .big-cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .big-cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.small-cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .small-cards {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}
@media (min-width: 71.875em) {
  .small-cards {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}

.card {
  background-color: var(--card);
  color: var(--text-sec);
  border-radius: 0.3125rem;
  width: 100%;
  position: relative;
  overflow: hidden;
  text-align: center;
  padding: 1.5625rem;
  transition: background-color 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card__small {
  display: grid;
  gap: 0 1rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  font-size: 0.875rem;
  font-weight: bold;
  text-align: left;
}
.card--facebook {
  border-top: 0.25rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.25rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.8125rem;
}
.card--instagram::before {
  content: "";
  height: 0.25rem;
  width: 100%;
  background: var(--instagram);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
.card--youtube {
  border-top: 0.25rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
  font-size: 0.75rem;
  font-weight: bold;
}
.card__count {
  color: var(--text-main);
  font-size: 3.5rem;
  font-weight: bold;
  margin-bottom: 1.5625rem;
  letter-spacing: -0.125rem;
}
.card__label {
  font-size: 0.75rem;
  color: var(--text-sec);
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: bold;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--smUp {
  color: var(--limegreen);
  justify-content: flex-end;
}
.card__change--down {
  color: var(--brightred);
}
.card__change--smDown {
  color: var(--brightred);
  justify-content: flex-end;
}

.icon {
  text-align: right;
}

.toggle {
  border: none;
  border-top: 2px solid var(--card);
  display: flex;
  gap: 1.125rem;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding-top: 1rem;
  cursor: default;
}
@media (min-width: 40em) {
  .toggle {
    border: none;
    margin-top: 0;
    padding-top: 0;
  }
}
.toggle__wrapper {
  height: 1.5rem;
  width: 3rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px 0 0;
  width: 1.25rem;
  height: 1.25rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid var(--text-main);
}
.toggle__background {
  display: block;
  background: var(--toggle);
  border-radius: 0.75rem;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--card);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 1.3125rem);
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-main);
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-sec);
}