@use '../util/' as *;

*, 
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    font-size: 100%;
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-inter);
    background-color: var(--background);
    color: var(--text-main);
}

img {
    display: inline-block;
}